.superInput {
  outline: none;
  border: none;
  width: 25%;
  font-size: 18px;
  color: darkgrey;
  border-bottom: 2px solid black;
  padding: 5px 10px;
  transition: border-color 0.3s ease, transform 0.3s ease;
  margin: 10px;
}

.small {
  outline: none;
  border: none;
  width: 10%;
  font-size: 14px;
  border-bottom: 1px solid black;
  padding: 0;
  margin: 0;
}

.superInput:focus {
  transform: scale(1.01);
}

.errorInput {
  border-bottom: 2px solid red;
  color: red;
}

.error {
  position: absolute;
  color: red;
}